import { useSearchParams } from "react-router-dom";
import AdmissionPendingFeesWrapper from "./AdmissionPendingFeesTabs";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import FinanceAllStudent from "../../FinanceManager/FinanceAllStudent/FinanceAllStudent";
import AdmissionPendingOutstanding from "./AdmissionPendingOutstanding";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import AdmissionPendingApplicableFees from "./AdmissionPendingApplicableFees";

const AdmissionPendingFeesConfig = ({ instituteId, studentAlias, aid }) => {
  const [searchParams] = useSearchParams();
  return (
    <UserMemberContainerWrapper>
      <AdmissionPendingFeesWrapper />
      {!searchParams.get("n") ? (
        <FinanceAllStudent
          instituteId={instituteId}
          studentAlias={studentAlias}
          invalidateAccess="unAuthorized"
        />
      ) : searchParams.get("n") === "outstanding" ? (
        <UserMemberContentWrapper>
          <AdmissionPendingOutstanding aid={aid} />
        </UserMemberContentWrapper>
      ) : searchParams.get("n") === "applicable" ? (
        <UserMemberContentWrapper>
          <AdmissionPendingApplicableFees aid={aid} />
        </UserMemberContentWrapper>
      ) : null}
    </UserMemberContainerWrapper>
  );
};

export default AdmissionPendingFeesConfig;
